.front {
  width: 90%;
  max-width: 800px;
  height: 40%;
  background-color: rgba(48, 48, 48, 0.9);
  position: absolute;
  z-index: 2;
  padding: 0 1%;
}

.back {
  width: 100%;
  height: 100vh;
  background-color: transparent;
  position: absolute;
  z-index: 1;
}

.modalContent {
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  margin: 10px 0;
}

.button {
  position: absolute;
  padding: 10px 23px;
  bottom: 0;
  right: 0;
}

#butt {
  right: 80px;
}

.h3 {
  width: fit-content;
}
