.icon {
  border-radius: 50px;
  border: 1px solid red;
  font-size: 25px;
  padding: 4px;
  cursor: pointer;
}

.icons {
  width: 100%;
  top: 15px;
  display: flex;
  align-items: center;
  background-color: transparent;
  position: absolute;
  padding-left: 800px;
}

.p {
  top: 25px;
  color: red;
  border-radius: 0 0 10px;
  position: absolute;
  font-size: normal;
  padding: 4px;
}

@media screen and (max-width: 1000px) {
  .icons {
    padding-left: 80%;
  }
}

@media screen and (max-width: 600px) {
  .icons {
    top: 0;
    display: grid;
    padding-left: 90%;
  }
  .icon {
    font-size: 20px;
  }
  .p {
    font-size: small;
    top: 70px;
    padding: 1px;
  }
}

.images {
  display: grid;
  justify-content: center;
  /* width: 100%; */
}

.header img {
  width: 100%;
  /* max-width: 900px; */
  min-height: 60px;
  max-height: 100px;
  object-fit: cover;
}

.link {
  background-color: transparent;
  display: flex;
  align-items: center;
}
